.container {
  width: 100%;

  // padding-left: 15px;
  // padding-right: 15px;
  height: auto;
  margin: 0 auto;

  @media screen and (min-width: 480px) {
    width: 480px;
    background-color: green;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    background-color: blue;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
    background-color: blueviolet;
  }
}
